import Watcher from './observer/watcher';
import { patch } from './vdom/patch';
export function lifeCycleMixin(Vue) {
    Vue.prototype._update = function(vnode){
        // ä¼ å…¥vnode å¾—åˆ°çœŸå®dom  æŒ‚åœ¨vm.$el ä¸Š
        const vm = this;
        vm.$el = patch(vm.$el, vnode)
    }
}
export function mountComponent(vm, el) {
    // vueåœ¨æ¸²æŸ“çš„è¿‡ç¨‹ä¸­ ä¼šåˆ›å»ºä¸€ä¸ª æ‰€è°“çš„æ¸²æŸ“watcher åªç”¨æ¥æ¸²æŸ“çš„
    // watcher å°±æ˜¯ä¸€ä¸ªå›è°ƒ æ¯æ¬¡æ•°æ®å˜åŒ– å°±ä¼šé‡æ–°æ‰§è¡Œwatcher
    // vue ä¸æ˜¯çº¯ç²¹çš„ MVVM æ¡†æ¶
    callHook(vm, 'beforeMount')
    function updateComponent() {
        console.log('updateComponent');
        vm._update(vm._render())
    }

    new Watcher(vm, updateComponent, () => { }, true);

    callHook(vm, 'mounted')
}


export function callHook(vm, hook) { // vm.$options
    let handlers = vm.$options[hook]; // å…¸å‹çš„å‘å¸ƒè®¢é˜…æ¨¡å¼
    if (handlers) {
        for (let i = 0; i < handlers.length; i++) { // [fn,fn,fn]
            handlers[i].call(vm); // æ‰€æœ‰çš„ç”Ÿå‘½å‘¨æœŸçš„this æŒ‡å‘çš„éƒ½æ˜¯å½“å‰çš„å®ä¾‹
        }
    }
}