let id = 0;
class Watcher {
    constructor(vm, exprOrFn, cb, options) {
        this.vm = vm;
        this.exprOrFn = exprOrFn;
        this.cb = cb;
        this.options = options;
        this.deps = [];
        this.depsId = new Set();
        this.id = id++;
        if (typeof exprOrFn === 'function') {
            this.getter = exprOrFn
        }
        this.get()
    }
    // new Watcherçš„æ—¶å€™å…ˆæŠŠæ¸²æŸ“watcherå®ä¾‹ å­˜åœ¨ Dep.targetä¸Š
    // this.getter() å°±æ˜¯è¦æ¸²æŸ“é¡µé¢ å¿…å°†definePropertyå–å€¼ 
    // æ­¤æ—¶æˆ‘å°±å¯ä»¥æ‹¿åˆ°å…¨å±€çš„Dep.target 
    // æ¯ä¸€ä¸ªå±æ€§éƒ½æœ‰ä¸€ä¸ªdep å–å€¼å°±æ˜¯å°†Dep.target å’Œ dep åŒå‘è®°å¿†
    // æ•°æ®å˜åŒ– é€šçŸ¥watcheræ›´æ–°
    get(){
        pushTarget(this)
        this.getter()
        popTarget()
    }
    // å±æ€§çš„getæ–¹æ³•è¢«æ‹¦æˆª æ‰§è¡Œdep.depend() å…¶å®æ˜¯Dep.target.addDep(this)
    // æŠŠdepä¼ ç»™watcheräº†  ç„¶åä¸¤è€…åŒå‘è®°å¿† å»é‡
    addDep(dep){
        if (!this.depsId.has(dep.id)) {
            this.depsId.add(dep.id)
            this.deps.push(dep)
            dep.addSub(this) // è®©å½“å‰dep è®¢é˜…è¿™ä¸ªwatcher
        }
    }
    // è™½ç„¶æ›´æ–° ä½†æˆ‘æ˜¯å¼‚æ­¥æ›´æ–° æ‰€ä»¥æŠŠwatcherå…ˆæ”¾è¿›å» æ²¡æœ‰é©¬ä¸Šæ‰§è¡Œ
    update(){
        queueWatcher(this)
    }
    // å†™ä¸€ä¸ªå¤‡ç”¨çš„runæ–¹æ³• ä¾›å¼‚æ­¥æ›´æ–°ä½¿ç”¨
    // this.get() ä¼šå¯¼è‡´é¡µé¢é‡æ–°æ¸²æŸ“
    run(){
        this.get()
    }
}


