//              å­—æ¯a-zA-Z_ - . æ•°ç»„å°å†™å­—æ¯ å¤§å†™å­—æ¯  
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`; // æ ‡ç­¾å
// ?:åŒ¹é…ä¸æ•è·   <aaa:aaa>
const qnameCapture = `((?:${ncname}\\:)?${ncname})`;
// startTagOpen å¯ä»¥åŒ¹é…åˆ°å¼€å§‹æ ‡ç­¾ æ­£åˆ™æ•è·åˆ°çš„å†…å®¹æ˜¯ (æ ‡ç­¾å)
const startTagOpen = new RegExp(`^<${qnameCapture}`); // æ ‡ç­¾å¼€å¤´çš„æ­£åˆ™ æ•è·çš„å†…å®¹æ˜¯æ ‡ç­¾å
// é—­åˆæ ‡ç­¾ </xxxxxxx>  
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // åŒ¹é…æ ‡ç­¾ç»“å°¾çš„ </div>
// <div aa   =   "123"  bb=123  cc='123'
// æ•è·åˆ°çš„æ˜¯ å±æ€§å å’Œ å±æ€§å€¼ arguments[1] || arguments[2] || arguments[2]
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // åŒ¹é…å±æ€§çš„
// <div >   <br/>
const startTagClose = /^\s*(\/?)>/; // åŒ¹é…æ ‡ç­¾ç»“æŸçš„ >
// åŒ¹é…åŠ¨æ€å˜é‡çš„  +? å°½å¯èƒ½å°‘åŒ¹é…
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g
export function parseHTML(html) {
    // ast æ ‘ è¡¨ç¤ºhtmlçš„è¯­æ³•
    let root; // æ ‘æ ¹ 
    let currentParent;
    let stack = []; // ç”¨æ¥åˆ¤æ–­æ ‡ç­¾æ˜¯å¦æ­£å¸¸é—­åˆ  [div]  è§£æå™¨å¯ä»¥å€ŸåŠ©æ ˆå‹ç»“æ„
    // <div id="app" style="color:red"><span>    helloworld {{msg}}   </span></div>

    // vue2.0 åªèƒ½æœ‰ä¸€ä¸ªæ ¹èŠ‚ç‚¹ å¿…é¡»æ˜¯html å…ƒç´ 

    // å¸¸è§æ•°æ®ç»“æ„ æ ˆ é˜Ÿåˆ— æ•°ç»„ é“¾è¡¨ é›†åˆ hashè¡¨ æ ‘
    function createASTElement(tagName, attrs) {
        return {
            tag: tagName,
            attrs,
            children: [],
            parent: null,
            type: 1 // 1 æ™®é€šå…ƒç´   3 æ–‡æœ¬
        }
    }
    // console.log(html)
    function start(tagName, attrs) { // å¼€å§‹æ ‡ç­¾ æ¯æ¬¡è§£æå¼€å§‹æ ‡ç­¾ éƒ½ä¼šæ‰§è¡Œæ­¤æ–¹æ³•
        let element = createASTElement(tagName, attrs);
        if (!root) {
            root = element; // åªæœ‰ç¬¬ä¸€æ¬¡æ˜¯æ ¹
        }
        currentParent = element;
        stack.push(element);
    }
    // <div> <span></span> hello world</div>   [div,span]
    function end(tagName) { // ç»“æŸæ ‡ç­¾  ç¡®ç«‹çˆ¶å­å…³ç³»
        let element = stack.pop();
        currentParent = stack[stack.length - 1];
        if (currentParent) {
            element.parent = currentParent;
            currentParent.children.push(element);
        }
    }

    function chars(text) { // æ–‡æœ¬
        text = text.replace(/\s/g, '');
        if (text) {
            currentParent.children.push({
                type: 3,
                text
            })
        }
    }
    // æ ¹æ® html è§£ææˆæ ‘ç»“æ„  </span></div>
    while (html) {
        let textEnd = html.indexOf('<');
        if (textEnd == 0) {
            const startTageMatch = parseStartTag();

            if (startTageMatch) {
                // å¼€å§‹æ ‡ç­¾
                start(startTageMatch.tagName, startTageMatch.attrs)
            }
            const endTagMatch = html.match(endTag);

            if (endTagMatch) {
                advance(endTagMatch[0].length);
                end(endTagMatch[1])
            }
            // ç»“æŸæ ‡ç­¾ 
        }

        // å¦‚æœä¸æ˜¯0 è¯´æ˜æ˜¯æ–‡æœ¬
        let text;
        if (textEnd > 0) {
            text = html.substring(0, textEnd); // æ˜¯æ–‡æœ¬å°±æŠŠæ–‡æœ¬å†…å®¹è¿›è¡Œæˆªå–
            chars(text);
        }
        if (text) {
            advance(text.length); // åˆ é™¤æ–‡æœ¬å†…å®¹
        }
    }

    function advance(n) {
        html = html.substring(n);
    }

    function parseStartTag() {
        const start = html.match(startTagOpen); // åŒ¹é…å¼€å§‹æ ‡ç­¾
        if (start) {
            const match = {
                tagName: start[1], // åŒ¹é…åˆ°çš„æ ‡ç­¾å
                attrs: []
            }
            advance(start[0].length);
            let end, attr;
            while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
                advance(attr[0].length);
                match.attrs.push({ name: attr[1], value: attr[3] || attr[4] || attr[5] })
            };
            if (end) {
                advance(end[0].length);
                return match;
            }
        }
    }

    return root;

}