export function isObject(obj) {
    return typeof obj === 'object' && obj !== null
}
const LIFECYCLE_HOOKS = [
    'beforeCreate',
    'created',
    'mounted',
    'beforeUpdate',
    'updated'
]
let strats = {};
function mergeHook(parentVal,childVal){ // []
    if(childVal){ // å¦‚æœ å­©å­æœ‰å€¼ 
        if(parentVal){ // çˆ¶äº²æœ‰å€¼ å°±ç›´æ¥æ‹¼æ¥
            return parentVal.concat(childVal)
        }else{ // å¦‚æœå­©å­æœ‰å€¼çˆ¶äº²æ²¡å€¼ å°±å°†å­©å­åŒ…è£…æˆæ•°ç»„
            return [childVal] // Vue.options.beforeCreate = [childVal]
        }
    }else{
        return parentVal; // ç›´æ¥è¿”å›çˆ¶äº²ï¼Œå› ä¸ºæ²¡æœ‰å­©å­  
    }
}
LIFECYCLE_HOOKS.forEach(hook=>{
    strats[hook] = mergeHook
})
export function mergeOptions(parent,child){ // {...parent,...child}  {a:1.b:2}  {a:{a:{b:2}}}
    const options = {}
    // å¦‚æœçˆ¶äº²å’Œå„¿å­é‡Œéƒ½æœ‰ä¸€ä¸ªå±æ€§ è¿™ä¸ªå±æ€§ä¸å†²çª 
    for(let key in parent){ // å¤„ç†çˆ¶äº²çš„æ‰€æœ‰å±æ€§
        mergeField(key);
    }
    for(let key in child){ // å¤„ç†å„¿å­çš„æ‰€æœ‰å±æ€§ï¼Œå¦‚æœçˆ¶äº²æœ‰çš„å€¼ åœ¨ç¬¬ä¸€ä¸ªå¾ªç¯ä¸­å°±å·²ç»å¤„ç†äº†
        if(!parent.hasOwnProperty(key)){
            mergeField(key);
        }
    }
    function mergeField(key){
        // ä¸¤ä¸ªç»„ä»¶é—´ dataæ˜¯å‡½æ•° 

        // å†™ä»£ç æ—¶å¾ˆå¿Œè®³ å„ç§if else if else 

        // ç­–ç•¥æ¨¡å¼ æ ¹æ®ä¸åŒçš„å±æ€§ è°ƒç”¨ä¸åŒçš„ç­–ç•¥  
        if(strats[key]){
            // è¿™é‡Œå°±åŒ…å«äº† mergeHookçš„é€»è¾‘
            options[key] = strats[key](parent[key],child[key])
        }else if(isObject(parent[key]) && isObject(child[key])){
            options[key] = Object.assign(parent[key],child[key])
        }else{
            if(child[key] == null){
                options[key] = parent[key];
            }else{
                options[key] = child[key]; // ç”¨å„¿å­çš„å€¼ ç›´æ¥è¦†ç›–æ‰ çˆ¶äº²çš„å€¼
            }
        }
    } // é¢è¯•æ—¶ ç»å¸¸ä¼šæåˆ°å¯¹è±¡é—´çš„åˆå¹¶
    return options
}
