// depå’Œwatcheræ˜¯ å¤šå¯¹å¤šçš„å…³ç³» 
// æ¯ä¸ªkeyå¯¹åº”ä¸€ä¸ªdep ä¸€ä¸ªdepå¯èƒ½å­˜ç€å¤šä¸ªwatcher ä¸€ä¸ªwatcherä¹Ÿå¯èƒ½è¢«å¤šä¸ªå±æ€§æ‰€ä¾èµ–
let id = 0;
class Dep{
    constructor(){
        this.id = id++;
        this.subs = []
    }
    // å¯¹è±¡çš„å±æ€§è¢«è®¿é—®  ä¼šæ‰§è¡Œ new Dep ç„¶å dep.depend  è¿™ä¸ªæ—¶å€™Dep.targetä¸Šå·²ç»æœ‰å®ä¾‹äº†
    // è¡¨é¢ä¸Šæ˜¯è°ƒç”¨äº†depçš„æ–¹æ³•  å…¶å®æ˜¯åœ¨æ“ä½œ watcher 
    // è®©watcherè®°å¿†dep  depè®°å¿†watcher åŒå‘è®°å¿†
    // å…ˆæ˜¯watcherå­˜dep  å†æ˜¯è°ƒç”¨depçš„addSubæ–¹æ³• å­˜watcher  è€Œä¸”æœ‰å»é‡åœ¨é‡Œé¢ ç§’
    depend(){
        Dep.target.addDep(this)
    }
    addSub(watcher){
        this.subs.push(watcher)
    }
    notify(){
        this.subs.forEach(watcher => watcher.update())
    }
}
// åˆå§‹åŒ–æ˜¯null
Dep.target = null;

const stack = [];
// æŠŠwatcherå­˜å‚¨åˆ°å…¨å±€
export function pushTarget(watcher){
    Dep.target = watcher
    stack.push(watcher)
}
// åˆ é™¤watcher
export function popTarget(){
    stack.pop()
    Dep.target = stack[stack.length - 1]
}

export default Dep;