const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g

function genProps(attrs){  // {id:'app',style:{color:red}}
    let str = ''
    for(let i = 0; i<attrs.length; i++){
        let attr = attrs[i]; // å–åˆ°æ¯ä¸€ä¸ªå±æ€§
        if(attr.name === 'style'){
            let obj = {}; //  color:red;background:green
            attr.value.split(';').forEach(item=>{
                let [key,value] = item.split(':');
                obj[key] = value;
            });
            attr.value = obj; // å°†åŸæ¥çš„å­—ç¬¦ä¸²æ¢æˆäº† åˆšæ‰æ ¼å¼åŒ–åçš„å¯¹è±¡
        }
        str += `${attr.name}:${JSON.stringify(attr.value)},`
    }
    return `{${str.slice(0,-1)}}`;
}
function gen(node){
    if(node.type === 1){
        return generate(node)
    }else{
        // æ–‡æœ¬çš„å¤„ç†
        let text = node.text;
        if(!defaultTagRE.test(text)){  // æœ‰å˜é‡ {{}}
            return `_v(${JSON.stringify(text)})` // _v('helloworld')
        }else{
            let tokens = []; // æ¯æ¬¡æ­£åˆ™ä½¿ç”¨è¿‡å éƒ½éœ€è¦é‡æ–°æŒ‡å®š lastIndex  aaaab
            let lastIndex =  defaultTagRE.lastIndex = 0;
            let match,index;
            while(match = defaultTagRE.exec(text)){
                index = match.index;
                // é€šè¿‡ lastIndex,ndex
                tokens.push(JSON.stringify(text.slice(lastIndex,index)));
                tokens.push(`_s(${match[1].trim()})`);
                lastIndex = index + match[0].length
            }
            if(lastIndex < text.length){
                tokens.push(JSON.stringify(text.slice(lastIndex)));
            }
            return `_v(${tokens.join('+')})`
        }
        // helloworld {{  msg  }}  aa {{bb}}  aaa   => _v('helloworld'+_s(msg)+"aa" + _s(bb))
    }
}  
function genChildren(el){ // <div><span></span> hello</div>
    const children = el.children;
    if(children){
        return children.map(c=>gen(c)).join(',')
    }else{
        return false
    }
}
export function generate(el){
    let children = genChildren(el); // ç”Ÿæˆå­©å­å­—ç¬¦ä¸²
    let code = `_c("${el.tag}",${
            el.attrs.length? `${genProps(el.attrs)}`  : undefined
        }${
            children? `,${children}` :''
        })`;

    return code;
}
