let oldArrayMethods = Array.prototype;  // è·å–æ•°ç»„åŸå‹ä¸Šçš„æ–¹æ³•

// åˆ›å»ºä¸€ä¸ªå…¨æ–°çš„å¯¹è±¡, æ ¹æ®åŸå‹é“¾å¯ä»¥æ‰¾åˆ°æ•°ç»„åŸå‹ä¸Šçš„æ–¹æ³•, è€Œä¸”æ‹“å±•æ–¹æ³•æ—¶ ä¸ä¼šå½±å“åŸæ•°ç»„çš„åŸå‹æ–¹æ³•
export let arrayMethods = Object.create(oldArrayMethods)

let methods = [
    'push',
    'pop',
    'unshift',
    'shift',
    'splice',
    'sort',
    'reserve'
]

methods.forEach(method => {
    arrayMethods[method] = function (...args) {
        const ob = this.__ob__
        let result = oldArrayMethods[method].apply(this, args)
        let inserted;
        switch (method) {
            case 'push':
            case 'unshift':
                inserted = args
                break;
            case 'splice':
                inserted = args.slice(2);
                break;
            default:
                break;
        }
        inserted && ob.observeArray(inserted)
        console.log('æ•°ç»„è¢«è§‚æµ‹äº†', inserted);
        return result;
    }
})
