let has = {}; // vueæºç é‡Œæœ‰çš„æ—¶å€™å»é‡ç”¨çš„æ˜¯set æœ‰çš„æ—¶å€™ç”¨çš„æ˜¯å¯¹è±¡æ¥å®ç°çš„å»é‡
let queue = [];

// è¿™ä¸ªé˜Ÿåˆ—æ˜¯å¦æ­£åœ¨ç­‰å¾…æ›´æ–°
function flushSchedulerQueue() {
    for (let i = 0; i < queue.length; i++) {
        queue[i].run();
    }
    queue = [];
    has = {};
}


export function queueWatcher(watcher) {
    const id = watcher.id;

    if (has[id] == null) {
        has[id] = true; // å¦‚æœæ²¡æœ‰æ³¨å†Œè¿‡è¿™ä¸ªwatcherï¼Œå°±æ³¨å†Œè¿™ä¸ªwatcheråˆ°é˜Ÿåˆ—ä¸­ï¼Œå¹¶ä¸”æ ‡è®°ä¸ºå·²ç»æ³¨å†Œ
        queue.push(watcher);
        nextTick(flushSchedulerQueue); // flushSchedulerQueue è°ƒç”¨æ¸²æŸ“watcher

    }
}
let callbacks = []; // [flushSchedulerQueue,fn]
let pending = false;
function flushCallbacksQueue() {
    callbacks.forEach(fn => fn());
    pending = false;
}

export function nextTick(fn) {
    callbacks.push(fn); // å‡½æ•°é˜²æŠ–
    if (!pending) { // true  äº‹ä»¶ç¯çš„æ¦‚å¿µ promise mutationObserver setTimeout setImmediate
        setTimeout(() => {
            flushCallbacksQueue();
        }, 0);
        pending = true
    }
}